import { IsString, IsNotEmpty, IsOptional, IsEnum, IsInt, IsDate, IsDateString } from 'class-validator';

export class CreateProjectDto {
  @IsString()
  @IsNotEmpty()
  name: string;

  @IsString()
  @IsOptional()
  description?: string;

  @IsEnum(['planned', 'on going', 'cancelled', 'completed', 'on hold'])
  status: 'planned' | 'on going' | 'cancelled' | 'completed' | 'on hold';

  @IsDateString()
  startDate: Date;

  @IsDateString()
  @IsOptional()
  endDate?: Date;

  @IsInt()
  createdBy: number;
}


export class UpdateProjectDto {
  @IsString()
  @IsNotEmpty()
  @IsOptional()
  name?: string;

  @IsString()
  @IsOptional()
  description?: string;

  @IsEnum(['planned', 'on going', 'cancelled', 'completed', 'on hold'])
  @IsOptional()
  status?: 'planned' | 'on going' | 'cancelled' | 'completed' | 'on hold';

  @IsDateString()
  @IsOptional()
  startDate?: Date;

  @IsDateString()
  @IsOptional()
  endDate?: Date;

  @IsInt()
  @IsOptional()
  createdBy?: number;
}
